
import Scroll from '../../../base/scroll/scroll'
import Checkbtn from '../../../base/checkbtn/checkbtn'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV2Data from 'vux/src/datas/china_address_v2.json'
import value2name from 'vux/src/filters/value2name.js'

export default {
name: "usersex",
components: {
Scroll,
XInput,
Group,
Checkbtn,
XAddress
},
data() {
return {
infos: [
'请填写淘宝常用收货地址，中奖下单必须与改地址一致！平台会定期检查，如发现不一致，将封号处理！',
'该地址将作为后期活动寄送的收货地址'
],
addressItem:{
name: '二口夭',
isDefault: true,
phone: '15345812564',
addressInfo: '浙江省 杭州市 余杭区 向往街999号乐富海邦园K座705'
},
isShowAdd: false,
addressOne: [],
addressOneName: '',
addressTwo: '',
addressName: '',
addressPhone: '',
isChecked: true,
btnSaveState: false,
addressData: ChinaAddressV2Data,
isShowAddress: false,
title: '默认'
}
},
watch: {
addressOne(newval) {
this.addressOneName = this.getName(newval)
this.regExpClicked()
},
addressTwo() {
this.regExpClicked()
},
addressName() {
this.regExpClicked()
},
addressPhone() {
this.regExpClicked()
}
},
methods: {
showAddAddress() {
this.isShowAdd = true
},

chooseAddress() {
this.isShowAddress = true
},

getName(value) {
return value2name(value, ChinaAddressV2Data)
},

regExpClicked() {

let hasCore = (this.addressOneName.length > 1)
let hasAdd = this.$refs.validAdd.valid
let hasName = this.$refs.validName.valid
let hasPhone = this.$refs.validPhone.valid
if (hasCore && hasAdd && hasName && hasPhone) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},

saveAddress() {

if (this.btnSaveState) {
console.log('保存')
}
},

updateAddress() {
this.isShowAdd = true
}
}
}



